      width: 280px;\n      margin: 0;\n      padding: 15px;\n      text-align: center;\n      /* vertical-align: middle; */\n      display: flex;\n      justify-content: center;\n      align-items: center; }\n.tailleEtiquette .shape {\n    z-index: -9999;\n    position: absolute;\n    width: inherit;\n    height: inherit; }\n.tailleEtiquette p {\n    font-family: \"avenir\";\n    height: calc(8.59cm*1.5);\n    width: calc(5.03cm*1.5);\n    top: -100px;\n    right: 100px;\n    transform: rotate(-90deg);\n    margin: 0 !important;\n    position: absolute;\n    text-align: justify; }\n.tailleEtiquette #texteDescriptif #ingredients #alc #warning {\n    position: absolute;\n    width: inherit;\n    height: inherit; }\n.tailleEtiquette #texteDescriptif {\n    /*Texte descriptif - TODO: variabiliser les valeurs et rendre responsive parceque ca va péter en autre orientation*/ }\n.tailleEtiquette #texteDescriptif p {\n      /*Texte descriptif police avenir taille 10.5 */\n      font-size: 16px;\n      padding: 15% 5% 35% 5%; }\n.tailleEtiquette #ingredients p {\n    /*Ingrédients police avenir taille 5  */\n    font-size: 10px;\n    padding: 80% 5% 0% 5%; }\n.tailleEtiquette #alc p {\n    /* % Alc 12 et gras police avenir taille 12*/\n    font-family: \"avenirBold\";\n    text-align: center;\n    font-size: 20px;\n    padding: 70% 5% 0% 5%; }\n.tailleEtiquette #warning p {\n    /* Avertissement abus d'alcool police avenir taille 4 avenir  */\n    font-size: 7px;\n    padding: 97% 2% 0% 3%; }\n\n/*# sourceMappingURL=data:application/json;base64, */"